@font-face {
    font-family: 'Open Sans';
    src: url('OpenSans-Light.eot');
    src: url('OpenSans-Light.eot?#iefix') format('embedded-opentype'),
        url('OpenSans-Light.woff2') format('woff2'),
        url('OpenSans-Light.woff') format('woff'),
        url('OpenSans-Light.ttf') format('truetype'),
        url('OpenSans-Light.svg#OpenSans-Light') format('svg');
    font-weight: 300;
    font-style: normal;
}

html {
    font-family: "Open Sans"
}

body {
    background: black;
    font-size: 16px;
    color: #ffffff;   
  }
  
.top { 
    display: flex;
    flex-direction: column;
    position: relative;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 30px;
        p {
        color: white;
        font-size: 1.1em;
        }
        select {
        color: black;
        background: white;
        width: 100%;
        border-radius: 5px;
        font-size: 16px;
        padding: 2px 0px 2px 5px;
        margin-top: 20px;
        margin-bottom: 35px;
        } 
}

.logo {
    width: 40%;
    margin-top: 120px; 
    margin-bottom: 20px;
}

figure {
    position: relative;
    img {  
    width: 100%;
    height: 100%;
    object-fit: cover;   
    }
    p { 
        position: absolute;
        display: block;
        height: auto; 
        background-color: rgba(0,0,0,.5);
        padding: 10px;
        line-height: 1.5em; 
        color: white;
        bottom: 0em;
        
    }      
}

@include tablet {
    .top {
        flex-direction: row;
        flex-wrap: wrap;
        margin-left: 130px;   
}
    .selection {
        margin-top: 130px;
        margin-left: 10px;
        font-size: 16px;
        select {
            font-size: 10px;
            width: 80%;
            padding: 0px 0px 0px 5px;
            border-radius: 3px;
            letter-spacing: -0.5px;
            margin-top: 10px;
        }
    }
    .logo {
        width: 30%;  
    }   

.display {
        display: grid;
        grid-template-rows: 1fr 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr;
        height: 100%;
    }

footer {
        z-index: 999;
    }
}

@include desktop {

.top {
        margin-left: 30px;   
}

.selection {
    margin-top: 130px;
    margin-left: 70px;
    font-size: 16px;
    select {
        font-size: 16px;
        width: 80%;
        padding: 0px 0px 0px 5px;
        border-radius: 3px;
        letter-spacing: -0.5px;
        margin-top: 20px;
    }
}

.logo {
    width: 20%;
}

    .display {
        display: grid;
        grid-template-rows: 1fr 1fr 1fr;
        grid-template-columns: 1fr 1fr 1fr 1fr;
        height: 100%;
    }
    
}

    
        


    




  
  
